import { useState } from "react";
import "./App.css";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import About from "./Components/About";
import Navbar from "./Components/Navbar";
import TextForm from "./Components/TextForm";
import Footer from "./Components/Footer";
import {
    HashRouter as Router,
    Switch,
    Route
} from "react-router-dom";

function App() {
	const [mode, setmode] = useState("light");
	const showAlert = (msg) => toast.success(msg);
    const toggleMode = () => {
		if (mode === "light") {
            setmode("dark");
            document.body.style.backgroundColor = "#161625";
			showAlert("Dark Mode ON");
        } else {
			setmode("light");
            document.body.style.backgroundColor = "#4A90E2";
			showAlert("Light Mode ON");
        }
    };
    return (
        <>
        <Router basename='/'>
            <Navbar title="Textlyzer" mode={mode} toggleMode={toggleMode} />
            <ToastContainer toastStyle={{ backgroundColor: "black", color: "grey"}} position="bottom-right" autoClose={2500} hideProgressBar={false} newestOnTop={false} closeOnClickrtl={false} pauseOnFocus Lossdraggable pauseOnHover/>
            <div className="container my-4">
                <Switch>
                    <Route exact path="/about">
                        <About mode={mode} />
                    </Route>
                    <Route exact path="/">
                    <TextForm showAlert={showAlert} heading="Enter your text below" mode={mode} />
                    </Route>
                </Switch>
            </div>
            <Footer/>
            </Router>
        </>
    );
}
export default App;