import React from 'react'
import { useState } from 'react'

export default function TextForm(props) {
    const [state, setstate] = useState("");
    const toUpperCase = ()=> {
        var newtext=state.toUpperCase();
        setstate(newtext);
        props.showAlert("Converted to Upper Case");
    }
    const toLowerCase = ()=> {
        var newtext=state.toLowerCase();
        setstate(newtext);
        props.showAlert("Converted to Lower Case");
    }
    const handleOnChange = (event)=> {
        setstate(event.target.value);
    }
    const copyToClipBoard = () => {
        navigator.clipboard.writeText(state);
        props.showAlert("Text copied to ClipBoard");
    }
    const removeExtraSpaces = () => {
        let newText = state.split(/[ ]+/);
        setstate(newText.join(" "));
        props.showAlert("Extra Spaces Removed");
    }
    const removeText = () => {
        setstate("");
        props.showAlert("Extra Spaces Removed");
    }
    const toTitleCase = () => {
        let words = state.split(" ");
        for (let i = 0; i < words.length; i++)
            words[i] = words[i][0].toUpperCase() + words[i].substring(1).toLowerCase();
        let newText = words.join(" ");
        setstate(newText);
        props.showAlert("Converted to Title Case");
    }
    return (
        <>
            <div>
                <h2 style={{color: props.mode==="light"?"black":"white"}}>{props.heading}</h2>
                <div className="mb-3 my-4">
                    <textarea className="form-control txtform" style={{backgroundColor: props.mode==="dark"?"black":"white"}} placeholder="Enter any text here." value={state} onChange={handleOnChange} id="exampleFormControlTextarea1" rows="10"></textarea>
                    <span class="focus-border">
                        <i></i>
                    </span>
                </div>
                <button disabled={state.length===0} className="btn btn-success my-2 mx-2" onClick={toUpperCase}>To Uppercase</button>
                <button disabled={state.length===0} className="btn btn-success my-2 mx-2" onClick={toLowerCase}>To Lowercase</button>
                <button disabled={state.length===0} className="btn btn-success my-2 mx-2" onClick={removeExtraSpaces}>Remove Extra Spaces</button>
                <button disabled={state.length===0} className="btn btn-success my-2 mx-2" onClick={toTitleCase}>To Titlecase</button>
                <button disabled={state.length===0} className="btn btn-success my-2 mx-2" onClick={copyToClipBoard}>Copy Text</button>
                <button disabled={state.length===0} className="btn btn-danger my-2 mx-2" onClick={removeText}>Clear Text</button>
            </div>
            <div className="container my-4">
                <h4 className="my-0" style={{color: props.mode==="light"?"black":"white"}}>  Textlyzer Results : </h4>
                <p style={{color: props.mode==="light"?"black":"white"}} className="resultline">Your text has <span className="results">{state===""?0:state.split(/\s+/).filter((elem)=>{return elem.length!==0}).length}</span> words and <span className="results">{state.length-state.split(" ").length+1}</span> characters.</p>
            </div>
        </>
    )
}